<template>
  <div class="mobile">
    <div class="bg-fixed">
      <div class="bg-fixed-0"></div>
      <div class="bg-fixed-1">
        <img src="../assets/images/m-bg-1-1.jpg" alt="">
      </div>
      <div class="bg-fixed-2">
        <img src="../assets/images/m-bg-1-2.jpg" alt="">
      </div>
    </div>

    <div class="name-form" v-show="nameFormShow">
      <input type="text" name="name" v-model="personName"
             placeholder="">
      <div>
        <button @click='search'></button>
      </div>
    </div>

    <div class="person-info" v-show="personInfoShow">
      <div class="pi-container">
        <div class="pi-name">{{personInfo.name}}</div>
        <div class="pi-lno">
          <div class="pi-lno-label">
            <span></span>
            <span></span>
            <span></span>
          </div>
          {{personInfo.lno}}
        </div>
        <div class="pi-sitNo">
          {{personInfo.sitNo}}
        </div>
        <div class="pi-extra-info">
          <div>{{personInfo.drawItem ? `${personInfo.drawItem}` : ''}}</div>
        </div>
      </div>
      <div class="pi-btn" @click="toggleShowListShow(true)"></div>
      <div style="font-size: 12px;color: #333333;margin-top: 6px;text-decoration:underline;" @click="exit()"></div>
      <div class="pi-star-shinning"></div>
    </div>

    <div class="show-list" v-show="showListShow">
      <div class="show-container" ref="showList">
        <div class="show-container-wrapper">
          <ul>
            <li v-for="(item, i) in shows" :key="i" class="sl-item">
              <div class="sl-a"><span>{{i+1}}</span></div>
              <div class="sl-b">
                <div class="sl-name">{{item.item}}</div>
                <div class="sl-des">{{item.des}}</div>
              </div>
            </li>
          </ul>
        </div>
        <div class="show-close" @click="toggleShowListShow(false)"></div>
      </div>
    </div>

    <div id="musicControl" :class="playing ? 'music-playing': 'music-stopping'">
      <a id="mc_play" @click.stop.prevent="playMusic()">
        <audio id="musicfx" loop autoplay preload>
          <source src="../assets/music/bubugao.mp3" type="audio/mpeg">
        </audio>
      </a>
    </div>

  </div>
</template>

<script>
import NyHttp from '../Api/index'
import FzsStorage from '../Api/FzsStorage'
import BScroll from 'better-scroll'

export default {
  name: 'mobile',
  data () {
    return {
      playing: false,
      playingDirty: false,
      nameFormShow: false,
      showListShow: false,
      personInfoShow: false,

      personName: '',
      personInfo: {
        name: '',
        lno: '',
        sitNo: '',
        drawItem: ''
      },
      shows: [],
      prizePersons: []
    }
  },
  components: {},
  methods: {
    exit () {
      FzsStorage.remove('personName')
      if (window.WeixinJSBridge) {
        window.WeixinJSBridge.call('closeWindow')
      } else {
        window.opener = null
        window.open('about:blank', '_self')
        window.close()
        window.setTimeout(() => {
          window.history.back()
        }, 500)
      }
    },
    playMusic () {
      const m = document.getElementById('musicfx')
      this.playing ? m.pause() : m.play()
      this.playing = !this.playing
    },
    tapPlayMusic () {
      console.info('tap')
      const m = document.getElementById('musicfx')
      m.load()
      m.play()
    },
    autoPlayMusic () {
      if (this.playingDirty) {
        return
      }
      // 
      const m = document.getElementById('musicfx')
      m.pause()
      m.play()

      // 
      window.addEventListener('touchstart', this.tapPlayMusic)
      window.addEventListener('click', this.tapPlayMusic)
    },
    toggleShowListShow (bool = true) {
      this.showListShow = bool
      // better scroll
      if (!this.scrollShowList) {
        this.$nextTick(() => {
          this.scrollShowList = new BScroll(document.querySelector('.show-container-wrapper'))
        })
      }
    },
    toggleMenu (item) {
      this.currentMenu = item.type
    },
    search () {
      if (this.personName.length < 2) {
        window.alert('~')
        return
      }
      NyHttp.checkAll(this.personName).then(data => {
        FzsStorage.set('personName', data.name)

        this.personInfo.name = data.name
        this.personInfo.lno = data.lno
        this.personInfo.sitNo = data.sitNo
        this.personInfo.drawItem = data.drawItem || ''

        this.shows = data.shows

        this.nameFormShow = false
        this.personInfoShow = true
      }, data => {
        FzsStorage.remove('personName')
        if (data.msg) {
          window.alert(data.msg)
        } else {
          window.alert('')
        }
        console.log(data)
      })
    }
  },
  mounted () {
    const personName = FzsStorage.get('personName')
    if (personName) {
      this.personName = personName
      this.search()
    } else {
      this.nameFormShow = true
      this.personInfoShow = false
    }

    this.$nextTick(() => {
      const m = document.getElementById('musicfx')
      m.onplaying = () => {
        this.playing = true
        console.log(' playing')
        if (this.playingDirty === false) {
          this.playingDirty = true
          console.info('')
          window.removeEventListener('touchstart', this.tapPlayMusic)
          window.removeEventListener('click', this.tapPlayMusic)
        }
      }
      m.onpause = () => {
        this.playing = false
      }
      this.autoPlayMusic()
    })

    /*
    document.addEventListener("WeixinJSBridgeReady", function () {
      bgAudio.load();
      bgAudio.play();
    }, false);
     */


    // this.$nextTick(() => {
    // })
  }
}
</script>

